<template>
  <div class="seller" ref="sellerComp">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{sellers.name}}</h1>
        <div class="desc">
          <star :size="36" :score="sellers.score"></star>
          <span class="text">({{sellers.ratingCount}})</span>
          <span class="text">月售{{sellers.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{sellers.minPrice}}</span>
              <span class="stress-after">元</span>
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{sellers.deliveryPrice}}</span>
              <span class="stress-after">元</span>
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{sellers.deliveryTime}}</span>
              <span class="stress-after">分钟</span>
            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <span class="icon-favorite" :class="{'active':favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
      </div>
      <split></split>
      <div class="bullentin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper">
          <p class="content">{{sellers.bulletin}}</p>
        </div>
        <ul v-if="sellers.supports" class="supports">
          <li class="support-item" v-for="(item,index) in sellers.supports">
            <span class="icon" :class="classMap[sellers.supports[index].type]"></span>
            <span class="text">{{sellers.supports[index].description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="pic-list">
          <ul class="pic-list">
            <li class="pic-item" v-for="pic in sellers.pics">
              <img :src="pic" width="120" height="90"/>
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h1 class="title">商家信息</h1>
        <ul>
          <li class="info-item" v-for="info in sellers.infos">{{info}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import star from '../../components/star/star.vue';
  import split from '../split/split.vue';
  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data: function () {
      return {
        favorite: false
      };
    },
    computed: {
      favoriteText: function () {
        return this.favorite ? '已收藏' : '未收藏';
      },
      sellers: function () {
        return this.seller;
      }
    },
    components: {
      star: star,
      split: split
    },
    methods: {
      _initScroll: function () {
        if (!this.sellerScroll) {
          this.sellerScroll = new BScroll(this.$refs['sellerComp'], {
            click: true
          });
        } else {
          this.sellerScroll.refresh();
        }
      },
      _initPics: function () {
        if (this.sellers.pics) {
          var picWidth = 120;
          var margin = 6;
          var width = (picWidth + margin) * this.sellers.pics.length;
          this.$refs['pic-list'].getElementsByTagName('ul')[0].style.width = width + 'px';
          this.$nextTick(function () {
            if (!this.picScroll) {
              this.picScroll = new BScroll(this.$refs['pic-list'], {
                scrollX: true,
                evenPassthrough: 'vertical'
              });
            } else {
              this.picScroll.refresh();
            }
          });
        }
      },
      toggleFavorite: function (event) {
        if (!event._constructed) {
          return;
        }
        this.favorite = !this.favorite;
      }
    },
    created: function () {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    mounted: function () {
      this._initScroll();
      this._initPics();
    },
    updated: function () {
      this._initScroll();
      this._initPics();
    }
  };
</script>

<style lang="less">
  @import "../../common/less/mixin.less";
  @url :"../../components/seller/";
  .seller{
    position: absolute;
    top: 174px;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    .overview{
      position: relative;
      padding: 18px;
      .title{
        margin-bottom: 8px;
        line-height: 14px;
        font-size: 14px;
        color:rgb(7,17,27);
      }
      .desc{
        padding-bottom:18px;
        border-bottom:1px solid rgba(7,17,27,0.1);
        font-size:0;
        .star{
          margin-right: 8px;
          display: inline-block;
          vertical-align: top;
        }
        .text{
          margin-right: 12px;
          display: inline-block;
          vertical-align: top;
          font-size: 10px;
          line-height: 18px;
          color: rgb(77, 85, 93);
        }
      }
      .remark{
        display: flex;
        padding-top: 18px;
        .block{
          flex: 1;
          text-align: center;
          border-right:1px solid rgba(7,17,27,0.1);
          &:last-child{
             border-right: none;
          }
          h2{
            margin-bottom: 4px;
            color:rgb(147,153,159);
            font-size:10px;
            line-height: 10px;
          }
          .content{
            font-size: 0;
            color:rgb(7,17,27);
            .stress{
              font-size: 24px;
              line-height: 24px;
            }
            .stress-after{
              font-size: 10px;
              line-height: 24px;
            }
          }
        }
      }
      .favorite{
        position: absolute;
        top: 18px;
        right: 11px;
        text-align: center;
        .icon-favorite{
          display: block;
          margin-bottom: 4px;
          line-height: 25px;
          font-size: 24px;
          color: #d4d6d9;
          &.active{
            color: rgb(240,20,20);
           }
        }
        .text{
          line-height: 10px;
          font-size: 10px;
          color: rgb(77,85,93);
        }
      }
    }
    .bullentin{
      padding: 18px 18px 0 18px;
      .title{
        margin-bottom: 8px;
        line-height: 14px;
        color: rgb(7,17,27);
      }
      .content-wrapper{
        padding: 0 12px 16px 12px;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        .content{
          font-size: 12px;
          color: rgb(240, 20, 20);
          line-height: 24px;
        }
      }
      .supports{
        padding: 0 18px;
        .support-item{
          padding: 16px 12px;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
          font-size: 0;
          &:last-child{
             margin-bottom:0;
             border-bottom: none;
          }
          .icon{
            display: inline-block;
            width: 16px;
            height: 16px;
            vertical-align: top;
            margin-right: 6px;
            background-size: 16px 16px;
            background-repeat: no-repeat;
            &.decrease{.bg-image("@{url}decrease_4");}
            &.discount{.bg-image("@{url}discount_4");}
            &.special{.bg-image("@{url}special_4");}
            &.invoice{.bg-image("@{url}invoice_4");}
            &.guarantee{.bg-image("@{url}guarantee_4");}
          }
          .text{
            line-height: 16px;
            font-size: 12px;
          }
        }
      }
    }
    .pics{
      padding: 18px;
      .title{
        margin-bottom: 8px;
        line-height: 14px;
        color: rgb(7,17,27);
        font-size: 14px;
      }
      .pic-wrapper{
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        .pic-list{
          font-size: 0;
          .pic-item{
            display: inline-block;
            margin-right: 6px;
            width: 120px;
            height: 90px;
            &:last-child{
              margin: 0;
            }
          }
        }
      }
    }
    .info{
      padding: 18px 18px 0 18px;
      .title{
        padding-bottom: 12px;
        line-height: 14px;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        color: rga(7, 17, 27);
        font-size: 14px;
      }
      .info-item{
        padding: 16px 12px;
        line-height: 16px;
        font-size: 12px;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        &:last-child{
           border: none;
         }
      }
    }
  }
</style>



