<template>
  <div class="ratingselect">
    <div class="rating-type">
      <span class="block positive" :class="{'active':selectTypeChange===2}" @click="select(2,$event)">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
      <span class="block positive" :class="{'active':selectTypeChange===0}" @click="select(0,$event)">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
      <span class="block negative" :class="{'active':selectTypeChange===1}" @click="select(1,$event)">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
    </div>
    <div class="switch" :class="{'on':onlyContentChange}" @click="toggleContent">
      <i class="icon-check_circle"></i>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      ratings: {
        type: Array,
        default: function () {
          return [];
        }
      },
      selectType: {
        type: Number,
        default: 2
      },
      onlyContent: {
        type: Boolean,
        default: false
      },
      desc: {
        type: Object,
        default: function () {
          return {
            all: '全部',
            positive: '满意',
            negative: '不满意'
          };
        }
      }
    },
    computed: {
      positives: function () {
        var ratingArr1 = [];
        this.ratings.forEach(function (rate) {
          if (rate.rateType === 0) {
            ratingArr1.push(rate);
          }
        });
        return ratingArr1;
      },
      negatives: function () {
        var ratingArr2 = [];
        this.ratings.forEach(function (rate) {
          if (rate.rateType === 1) {
            ratingArr2.push(rate);
          }
        });
        return ratingArr2;
      },
      selectTypeChange: function () {
        return this.selectType;
      },
      onlyContentChange: function () {
        return this.onlyContent;
      }
    },
    methods: {
      select: function (type, event) {
        if (!event._constructed) {
          return;
        }
        this.$bus.emit('ratingtype.select', type);
      },
      toggleContent: function (event) {
        if (!event._constructed) {
          return;
        }
        this.$bus.emit('content.toggle', !this.onlyContentChange);
      }
    }
  };
</script>

<style lang="less">
  .ratingselect{
    .rating-type{
      padding: 18px 0;
      margin: 0 18px;
      border-bottom:1px solid rgba(7,17,27,0.1);
      font-size: 0;
      .block{
        display: inline-block;
        padding: 8px 12px;
        font-size: 12px;
        margin-right: 8px;
        border-radius:1px;
        color:rgb(77,85,93);
        &.active{
          color:#fff;
        }
        .count{
          margin-left: 2px;
          line-height: 16px;
          font-size: 8px;
        }
        &.positive{
           background: rgba(0, 160, 220, 0.2);
           &.active{
              background: rgb(0, 160, 220);
           }
        }
        &.negative{
          background:rgba(77,85,93,0.2);
          &.active{
             background: rgb(77,85,93);
          }
        }
      }
    }
    .switch{
      padding: 12px 18px;
      line-height: 24px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      color:rgb(147,153,159);
      font-size: 0;
      &.on{
        .icon-check_circle{
          color: #00c850;
        }
      }
      .icon-check_circle{
        display: inline-block;
        vertical-align: top;
        margin-right: 4px;
        font-size: 24px;
      }
      .text{
        display: inline-block;
        vertical-align: top;
        font-size: 12px;
      }
    }
  }

</style>



