<template>
  <div class="cartcontrol">
    <transition name="cart-move">
      <div class="cart-decrease icon-remove_circle_outline" v-show="food.count>0" @click.stop.prevent="decreaseCart($event)"></div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <div class="cart-add icon-add_circle" @click.stop.prevent="addCart($event)"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      food: {
        type: Object
      }
    },
    methods: {
      addCart: function (event) {
        if (!event._constructed) {
          return;
        }
        if (!this.food.count) {
          this.$set(this.food, 'count', 1);
        } else {
          this.food.count++;
        }
      },
      decreaseCart: function (event) {
        if (!event._constructed) {
          return;
        }
        if (this.food.count > 0) {
          this.food.count--;
        }
      }
    }
  };
</script>

<style lang="less">
  .cartcontrol{
    font-size:0;
    .cart-decrease,.cart-add{
      display: inline-block;
      padding: 6px;
      line-height: 24px;
      font-size: 24px;
      color: rgb(0, 15, 220);
    }
    .cart-count{
      display: inline-block;
      vertical-align: top;
      width: 12px;
      padding-top: 6px;
      line-height: 24px;
      text-align: center;
      font-size: 10px;
      color: rgb(147, 153, 159);
    }
    .cart-move-enter{
      opacity: 0;
      transform: translate3D(24px,0,0) rotate(180deg);
    }
    .cart-move-enter-active{
      transition: all 0.4s linear;
    }
    .cart-move-leave-active{
      opacity: 0;
      transform: translate3D(24px,0,0) rotate(180deg);
      transition: all 0.4s linear;
    }
  }
</style>



