<template>
    <div class="ratings" ref="rate">
      <div class="ratings-content">
        <div class="overview">
          <div class="overview-left">
            <h1 class="score">{{seller.score}}</h1>
            <div class="title">综合评分</div>
            <div class="rank">高于周边商家{{seller.rankRate}}%</div>
          </div>
          <div class="overview-right">
            <div class="score-wrapper">
              <span class="title">服务态度</span>
              <star :size="36" :score="seller.serviceScore" class="star"></star>
              <span class="score">{{seller.serviceScore}}</span>
            </div>
            <div class="score-wrapper">
              <span class="title">商品评分</span>
              <star :size="36" :score="seller.foodScore" class="star"></star>
              <span class="score">{{seller.foodScore}}</span>
            </div>
            <div class="delivery-wrapper">
              <span class="title">送达时间</span>
              <span class="time">{{seller.deliveryTime}}分钟</span>
            </div>
          </div>
        </div>
        <split></split>
        <ratingselect :select-type="selectType" :only-content="onlyContent" :ratings="ratings"></ratingselect>
        <div class="rating-wrapper">
          <ul>
            <li v-for="rating in ratings" class="rating-item" v-show="needShow(rating.rateType,rating.text)">
              <div class="avatar">
                <img :src="rating.avatar" width="28" height="28"/>
              </div>
              <div class="content">
                <h1 class="name">{{rating.username}}</h1>
                <div class="star-wrapper">
                  <star :size="24" :score="rating.score"></star>
                  <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
                </div>
                <p class="text">{{rating.text}}</p>
                <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                  <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
                  <span v-for="item in rating.recommend" class="item">{{item}}</span>
                </div>
                <div class="time">
                  {{rating.rateTime | formatDate}}
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
  import star from '../../components/star/star.vue';
  import BScroll from 'better-scroll';
  import ratingselect from '../ratingselect/ratingselect.vue';
  import split from '../split/split.vue';
  import {formatDate} from '../../common/js/date.js';
  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data: function () {
      return {
        ratings: [],
        selectType: 2,
        onlyContent: false
      };
    },
    components: {
      star: star,
      ratingselect: ratingselect,
      split: split
    },
    filters: {
      formatDate: function (time) {
        var date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    },
    created: function () {
      this.$http.get('/api/ratings').then(function (response) {
        response = response.body;
        if (response.errno === 0) {
          this.ratings = response.data;
        }
        this.$nextTick(function () {
          this._initScroll();
        });
      });
      var that = this;
      this.$bus.on('ratingtype.select', function (type) {
        that.selectType = type;
        this.$nextTick(function () {
          that._initScroll();
        });
      });
      this.$bus.on('content.toggle', function (val) {
        that.onlyContent = val;
        this.$nextTick(function () {
          that._initScroll();
        });
      });
    },
    methods: {
      _initScroll: function () {
        if (!this.ratingScroll) {
          this.ratingScroll = new BScroll(this.$refs['rate'], {
            click: true
          });
        } else {
          this.ratingScroll.refresh();
        }
      },
      needShow: function (type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === 2) {
          return true;
        } else {
          return type === this.selectType;
        }
      }
    }
  };
</script>

<style lang="less">
  .ratings{
    position: absolute;
    top: 174px;
    width: 100%;
    bottom: 0;
    left: 0;
    overflow: hidden;
    .overview{
      display: flex;
      padding:18px 0;
      .overview-left{
        flex: 0 0 137px;
        width: 137px;
        border-right: 1px solid rgba(7, 17, 27, 0.7);
        text-align: center;
        padding: 6px 0;
        @media only screen and (max-width: 320px) {
          flex: 0 0 120px;
          width:120px;
        }
        .score{
          margin-bottom: 6px;
          font-size: 24px;
          line-height: 28px;
          color:rgb(255,153,0);
        }
        .title{
          margin-bottom: 8px;
          font-size: 12px;
          color:rgb(7,17,27);
          line-height: 12px;
        }
        .rank{
          font-size: 10px;
          color:rgb(147,153,159);
          line-height: 10px;
        }
      }
      .overview-right{
        flex: 1;
        padding:6px 0 6px 24px;
        @media only screen and (max-width: 320px) {
          padding-left: 6px;
        }
        .score-wrapper{
          margin-bottom: 8px;
          font-size: 0;
          .title{
            display: inline-block;
            vertical-align: top;
            padding-right: 12px;
            font-size: 12px;
            color:rgb(7,17,27);
            line-height: 18px;
          }
          .star{
            display: inline-block;
            vertical-align: top;
          }
          .score{
            display: inline-block;
            vertical-align: top;
            margin:0 12px;
            font-size: 12px;
            color:rgb(255,153,0);
            line-height: 18px;
            @media only screen and (max-width: 320px) {
              margin:0 0 0 6px;
            }
          }
        }
        .delivery-wrapper{
          font-size: 0;
          .title{
            padding-right: 12px;
            font-size: 12px;
            color:rgb(7,17,27);
            line-height: 18px;
          }
          .time{
            font-size: 12px;
            color:rgb(147,153,159);
            line-height: 18px;
          }
        }
      }
    }
    .rating-wrapper{
      padding:0 18px;
      .rating-item{
        display: flex;
        padding: 18px 0;
        border-bottom:1px solid rgba(7,17,27,0.1);
        .avatar{
          flex: 0 0 28px;
          width: 28px;
          margin-right: 12px;
          img{
            border-radius: 50%;
          }
        }
        .content{
          position: relative;
          flex:1;
          .name{
            margin-bottom: 4px;
            line-height: 12px;
            font-size: 10px;
            color: rgb(7, 17, 27);
          }
          .star-wrapper{
            margin-bottom: 6px;
            font-size: 0;
            .star{
              display: inline-block;
              margin-right: 6px;
              vertical-align: top;
            }
            .delivery{
              display: inline-block;
              vertical-align: top;
              font-size: 10px;
              color: rgb(147, 153, 159);
            }
          }
          .text{
            margin-bottom: 8px;
            line-height: 18px;
            color: rgb(7, 17, 27);
            font-size: 12px;
          }
          .recommend{
            line-height: 16px;
            .icon-thumb_up, .item, .icon-thumb_down{
              display: inline-block;
              margin: 0 8px 4px 0;
              font-size: 9px;
            }
            .icon-thumb_up{
              color: rgb(0, 160, 220);
            }
            .icon-thumb_down{
              color: rgb(183,187,191);
            }
            .item{
              padding: 0 6px;
              border: 1px solid rgba(7, 17, 27, 0.1);
              border-radius: 1px;
              color: rgb(147, 153, 159);
              background-color: #fff;
            }
          }
          .time{
            position: absolute;
            top: 0;
            right: 0;
            font-size: 10px;
            color: rgb(147, 153, 159);
            line-height: 12px;
          }
        }
      }
    }
  }
</style>



