<template>
  <div class="self-title">
    <div class="self-line"></div>
    <div class="self-text">{{selftext}}</div>
    <div class="self-line"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: ['selftext']
  };
</script>

<style lang="less">
  .self-title{
      display: flex;
      width: 80%;
      margin: 28px auto 24px;
    .self-line{
      flex:1;
      position: relative;
      top: -6px;
      border-bottom: 1px solid rgba(255,255,255,0.2);
    }
    .self-text{
      padding: 0 12px;
      font-size: 14px;
      font-weight: 700;
    }
  }
</style>



