<template>
  <div class="star" :class="starType">
    <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item"></span>
  </div>
</template>

<script type="text/ecmascript-6">

  var LENGTH = 5;
  var CLS_ON = 'on';
  var CLS_OFF = 'off';
  var CLS_HALF = 'half';
  export default {
    props: ['size', 'score'],
    computed: {
      starType: function() {
        return 'star-' + this.size;
      },
      itemClasses: function() {
        var result = [];
        var score = Math.floor(this.score * 2) / 2;
        var hasDecimal = score % 1 !== 0;
        var integer = Math.floor(score);
        for (var i = 0; i < integer; i++) {
          result.push(CLS_ON);
        }
        if (hasDecimal) {
          result.push(CLS_HALF);
        }
        while (result.length < LENGTH) {
          result.push(CLS_OFF);
        }
        return result;
      }
    }
  };
</script>

<style lang="less">
  @import "../../common/less/mixin.less";

  @url :"../../components/star/";

  .star{
    font-size: 0;
    .star-item{
      display: inline-block;
      background-repeat: no-repeat;
    }
    &.star-48{
      .star-item{
        width: 20px;
        height: 20px;
        margin-right: 22px;
        background-size: 20px 20px;
        &:last-child{
           margin-right: 0px;
        }
        &.on{
         .bg-image('@{url}star48_on');
        }
        &.half{
         .bg-image('@{url}star48_half');
        }
        &.off{
         .bg-image('@{url}star48_off');
        }
      }
    }
    &.star-36{
      .star-item{
        width: 15px;
        height: 15px;
        margin-right: 6px;
        background-size: 15px 15px;
      &:last-child{
         margin-right: 0px;
       }
      &.on{
       .bg-image('@{url}star36_on');
       }
      &.half{
       .bg-image('@{url}star36_half');
       }
      &.off{
       .bg-image('@{url}star36_off');
       }
      }
    }
    &.star-24{
      .star-item{
        width: 10px;
        height: 10px;
        margin-right: 3px;
        background-size: 10px 10px;
      &:last-child{
         margin-right: 0px;
       }
      &.on{
       .bg-image('@{url}star24_on');
       }
      &.half{
       .bg-image('@{url}star24_half');
       }
      &.off{
       .bg-image('@{url}star24_off');
       }
      }
    }
  }
</style>



