<template>
    <div class="goods">
      <div class="menu-wrapper" ref="menu-wrapper">
        <ul>
          <li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}" @click="selectManu(index ,$event)">
            <span class="text border-by-dpi">
              <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
            </span>
          </li>
        </ul>
      </div>
      <div class="foods-wrapper" ref="foods-wrapper">
        <ul>
          <li v-for="item in goods" class="food-list food-list-hook">
            <h1 class="title">{{item.name}}</h1>
            <ul>
              <li v-for="food in item.foods" class="food-item border-by-dpi" @click="selectFood(food,$event)">
                <div class="icon">
                  <img :src="food.icon" alt="食品图标哦" width="57" height="57"/>
                </div>
                <div class="content">
                  <h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  <div class="extra">
                    <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{food.price}}</span>
                    <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol :food="food"></cartcontrol>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <shopcart :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice" :select-foods="selectFoods"></shopcart>
      <food :food="selectedFood"></food>
    </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import shopcart from '../shopcart/shopcart.vue';
  import cartcontrol from '../cartcontrol/cartcontrol.vue';
  import food from '../food/food.vue';
  export default {
    props: ['seller'],
    data: function () {
      return {
        goods: [],
        listHeight: [],
        scrollY: 0,
        selectedFood: {}
      };
    },
    components: {
      shopcart: shopcart,
      cartcontrol: cartcontrol,
      food: food
    },
    computed: {
      currentIndex: function () {
        for (var i = 0; i < this.listHeight.length; i++) {
          var height1 = this.listHeight[i];
          var height2 = this.listHeight[i + 1];
          if ((this.scrollY >= height1 && this.scrollY < height2) || !height2) {
            return i;
          }
        }
        return 0;
      },
      selectFoods: function () {
        var foods = [];
        this.goods.forEach(function (good) {
          good.foods.forEach(function (food) {
            if (food.count) {
              foods.push(food);
            }
          });
        });
        return foods;
      }
    },
    created: function () {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      this.$http.get('/api/goods').then(function (response) {
        response = response.body;
        if (response.errno === 0) {
          this.goods = response.data;
//          此函数nextTick是在数据被渲染之后才会触发内部的回调
          this.$nextTick(function () {
            this._initScroll();
            this._calcHeight();
          });
        }
      });
    },
    methods: {
      _initScroll: function () {
        this.menuScroll = new BScroll(this.$refs['menu-wrapper'], {
          click: true
        });
        this.foodScroll = new BScroll(this.$refs['foods-wrapper'], {
          probeType: 3,
          click: true
        });
        var that = this;
        this.foodScroll.on('scroll', function (pos) {
          that.scrollY = Math.abs(Math.round(pos.y));
        });
      },
      _calcHeight: function () {
        var foodList = this.$refs['foods-wrapper'].getElementsByClassName('food-list-hook');
        var height = 0;
        this.listHeight.push(height);
        for (var i = 0; i < foodList.length; i++) {
          var item = foodList[i];
          height += item.clientHeight;
          this.listHeight.push(height);
        }
      },
      selectManu: function (index, event) {
//        只有自己派发的事件被定义了，此属性才会被判断为true
        if (!event._constructed) {
          return;
        }
        var foodList = this.$refs['foods-wrapper'].getElementsByClassName('food-list-hook');
        var el = foodList[index];
        this.foodScroll.scrollToElement(el, 500);
      },
      selectFood: function (food, event) {
        if (!event._constructed) {
          return;
        }
        this.selectedFood = food;
        this.$bus.emit('showFoodDetail');
      }
    }
  };
</script>
<style lang="less" rel="stylesheet/less">
  @import "../../common/less/mixin.less";
  @url: "../../components/goods/";
  .goods{
    display: flex;
    position: absolute;
    top: 174px;
    bottom: 46px;
    width: 100%;
    overflow: hidden;
    .menu-wrapper{
      flex: 0 0 80px;
      width: 80px;
      background: #f3f5f7;
      .menu-item{
        display: table;
        height: 54px;
        width: 56px;
        line-height: 14px;
        padding: 0 12px;
        &.current{
           position: relative;
           margin-top: -1px;
           z-index: 10;
           background: #fff;
           font-weight: 700;
           .text{
             .border-none();
           }
        }
        .icon{
          display: inline-block;
          vertical-align: top;
          width: 12px;
          height: 12px;
          margin-right: 2px;
          background-size: 12px 12px;
          background-repeat: no-repeat;
          &.decrease{.bg-image("@{url}decrease_3");}
          &.discount{.bg-image("@{url}discount_3");}
          &.special{.bg-image("@{url}special_3");}
          &.invoice{.bg-image("@{url}invoice_3");}
          &.guarantee{.bg-image("@{url}guarantee_3");}
        }
        .text{
          display: table-cell;
          width: 56px;
          vertical-align: middle;
          font-size: 12px;
          .border-1px(rgba(7,17,27,0.1));
        }
      }
    }
    .foods-wrapper{
      flex: 1;
      .title{
        padding-left: 14px;
        height: 26px;
        line-height: 26px;
        border-left:2px solid #d9dde1;
        font-size: 12px;
        color: rgb(147, 153, 159);
        background: #f3f5f7;
      }
      .food-item{
        display: flex;
        margin: 18px;
        padding-bottom: 18px;
        .border-1px(rgba(7,17,27,0.1));
        &:last-child{
         margin-bottom: 0;
         .border-none();
        }
        .icon{
          flex: 0 0 57px;
          margin-right: 10px;
        }
        .content{
          flex: 1;
          .name{
            margin: 2px 0 8px 0;
            height: 14px;
            line-height: 14px;
            font-size: 14px;
            color: rgb(7,17,27);
          }
          .desc, .extra{
            line-height: 10px;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
          .desc{
            line-height: 12px;
            margin-bottom: 8px;
          }
          .extra{
            .count{
              margin-right: 12px;
            }
          }
          .price{
            font-weight: 700;
            line-height: 24px;
            .now{
              margin-right: 8px;
              font-size: 14px;
              color: rgb(240, 20, 20);
            }
            .old{
              text-decoration: line-through;
              font-size: 10px;
              color: rgb(147, 153, 159);
            }
          }
          .cartcontrol-wrapper{
            position: absolute;
            right: 0;
            bottom: 12px;
          }
        }
      }
    }
  }
</style>



